<template>
  <div style="max-width: 100%;box-sizing: border-box" class="feedbackDetail">
    <myHeader :isBack="true" title="问题反馈"></myHeader>
    <div class="seat-head"></div>

    <div class="repeat-list" v-if="questionData.replydetails">
      <div class="top">
        <span class="color-block  color-block-answer"></span>
        <span>回复：</span>
      </div>
      <div class="main">
        <p class="repeat-text">{{questionData.replypersonname}}：{{questionData.replydetails}}</p>
        <div class="repeat-time">
          <span>{{questionData.replydate}}</span>
          <!--<span class="detail-time">18:30:01</span>-->
        </div>
      </div>
    </div>

    <div class="repeat-list">
      <div class="top">
        <span class="color-block color-block-question"></span>
        <span>提问：</span>
      </div>
      <div class="main">
        <p class="repeat-text">{{questionData.custname}}：{{questionData.details}}</p>
        <div class="repeat-time">
          <span>{{questionData.bizdate}}</span>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  import myHeader from '@/components/Header'
  import {queryQuestionInfoById} from  '@/api/feedback'
  import {getId} from  '@/utils/tool'

  export default {
    name: 'feedbackDetail',
    data() {
      return {
       questionData:{
         replypersonname:'',
         replydetails:'',
         replyDate:'',
         questionName:'',
         details:'',
         bizdate:''
       }
      }
    },
    components: {
      myHeader
    },
    created(){
      this._getQuestionDetail();
    },
    methods: {
        _getQuestionDetail(){
          const rcordId = this.$route.params.recordId;
          queryQuestionInfoById(rcordId).then((res)=>{
            this.questionData = res.data;
          }).catch((err)=>{
            console.log(err)
          })
        }
    }
  }
</script>

<style lang="scss">
  .feedbackDetail {
    min-height: 100vh;
    background-color: #F8F8F8;

    .head {
      background-color: #FFF;
    }

    .repeat-list {
      padding: .25rem .25rem .1rem .25rem;
      font-size: .16rem;
      background: #fff;
      .top {
        margin-bottom: 13px;
        .color-block-answer{
          background: url("~@/assets/icon-answer.png");
        }
        .color-block-question{
          background: url("~@/assets/icon-question.png");
        }
        .color-block {
          margin-right: .05rem;
          vertical-align: middle;
          display: inline-block;
          width: .13rem;
          height: .13rem;
          background-size: .13rem .13rem;
        }
      }

      .main {
        color: #7E7E7E;
        font-size: .12rem;
        line-height: 1.8;
        .repeat-text {
          margin-bottom: 10px;
        }
        .repeat-time {
          text-align: right;
          .detail-time {
            padding-left: .1rem;
          }
        }
      }

      border-bottom: 1px solid #ddd;

    }


  }
</style>



